const profile = createReducer(initialState, {
    [actions.SET] (state, action){
        return {
            ...state,
            selectedProject: action.data
        }
    },
    [actions.TOGGLE_MODAL] (state, action){
        return {
            ...state,
            openedModal: action.data
        }
    }
})

export default combineReducers({
    profile
})