import React from 'react';
import './style.css';
import {connect} from 'react-redux'
import {setProject} from '../../store/actions';
import {toggleModal} from '../../store/actions';

class ProfileExperience extends React.Component {

    constructor(){
        super();
        this.items = [];
    }

    componentDidMount(){
        var point = document.querySelector('.profile-experience svg').createSVGPoint();

        function getCoordinates(e, svg) {
            point.x = e.clientX;
            point.y = e.clientY;
            return point.matrixTransform(svg.getScreenCTM().inverse());
        }
        
        function Item(config) {
            Object.keys(config).forEach(function (item) {
                this[item] = config[item];
            }, this);
            this.el.addEventListener('mousemove', this.mouseMoveHandler.bind(this));
            this.el.addEventListener('touchmove', this.touchMoveHandler.bind(this));
        }
        
        Item.prototype = {
            update: function update(c) {
                this.clip.setAttribute('cx', c.x);
                this.clip.setAttribute('cy', c.y);
            },
            mouseMoveHandler: function mouseMoveHandler(e) {
                this.update(getCoordinates(e, this.svg));
            },
            touchMoveHandler: function touchMoveHandler(e) {
                e.preventDefault();
                var touch = e.targetTouches[0];
                if (touch) return this.update(getCoordinates(touch, this.svg));
            }
        };
        [].slice.call(document.querySelectorAll('.profile-experience .item'), 0).forEach((item, index) => {
            this.items.push(new Item({
                el: item,
                svg: item.querySelector('.profile-experience svg'),
                clip: document.querySelector('#clip-'+index+' circle'),
            }));
        });

        [].slice.call(document.querySelectorAll('.profile-experience button'), 0).forEach((button) => {
            button.addEventListener('click', function(e){document.body.className = e.currentTarget.className;});
        });
    }

    showModal(index) {
        const {dispatch} = this.props;
        dispatch(setProject(index));
        // var modal = document.getElementById("myModal");
        // var appContent = document.getElementById("appContent");
        // appContent.style.display = 'none';
        // modal.style.display="block";     
        dispatch(toggleModal(true));   
    }

    renderItems(){
        
            return this.props.projects.map( it => {
                return (<div key={`key-${it.index}`} className='item' onClick={()=>{this.showModal(it.index)}}>
                    <svg preserveAspectRatio='xMidYMid slice' viewBox='0 0 300 200'>
                        <defs>
                            <clipPath id={`clip-${it.index}`} >
                                <rect x="0" y="0" width="300px" height="200px"/>
                                <circle cx='0'  cy='0' fill='#000' r='150px'></circle>                                                
                            </clipPath>
                            
                        </defs>
                        <text className='svg-text' dy='.3em' x='50%' y='50%'>
                            {it.title || ''}
                        </text>
                        <g className="item-container" clipPath={`url(#clip-${it.index})`}>
                            <image height='100%' preserveAspectRatio='xMinYMin slice' width='100%' xlinkHref={`${it.link || 'update.png'}`}></image>
                            <text className='svg-masked-text' dy='.3em' x='50%' y='50%'>
                                {it.title || ''}
                            </text>
                        </g>
                    </svg>
                </div>)
            }
        )
    }

    render(){
        console.log(this)
        return (
                <div className='profile-experience'>
                    <header>
                        <h1>Projects</h1>
                        <div className='small'>
                            <ul>
                                <li>Python</li>
                                <li>Django</li>
                                <li>Flask</li>                                
                                <li>JavaScript</li>
                                <li>TypeScript</li>
                                <li>NodeJS</li>
                                <li>VueJS</li>
                                <li>ReactJS</li>
                                <li>React Native</li>
                                <li>SQL</li>
                                <li>MongoDB</li>
                                <li>GraphQL</li>
                                <li>HTML/CSS</li>
                                <li>AWS</li>
                                <li>Docker</li>
                                <li>PHP</li>
                                <li>Laravel</li>
                            </ul>
                        </div>
                    </header>
                    <main>
                        <div className='items'>
                            {this.renderItems()}
                        </div>
                    </main>
                </div>
        )
    }
}

export default connect(state => ({
    projects: state.profile.projects
}))(ProfileExperience)