import React from 'react'
import './style.css'
export default class ProfileReference extends React.Component {
    constructor(){
        super();
        this.email = "likeredapple@outlook.com";
        this.phone = "+86 13260367159";
    }

    render(){
        return (
            <div className="profile-reference">
                <h1>Contact</h1>
                <div className="contact-menu">
                    <form action="" method="post">
                        
                        <div>
                            <input type="text" placeholder="Your Name" id="fname" name="fname"/>
                        </div>
                        
                        <div>
                            <input type="email" placeholder="Your Email" id="lname" name="lname"/>
                        </div>
                        
                        <div>
                            <textarea id="dname" placeholder="Your Message" name="dname"></textarea>
                        </div>
                        <div>
                            <input id="contactSubmit" type="submit" value="Send"/>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}