import React from 'react'
import './style.css'
import {connect} from 'react-redux'
import {toggleModal} from '../../store/actions';

class ProfileModal extends React.Component {

    hideModal(){
        // var modal = document.getElementById("myModal");
        // var appContent = document.getElementById("appContent");
        // modal.style.display = "none";
        // appContent.style.display = 'block';
        const {dispatch} = this.props;
        dispatch(toggleModal(false));
    }
    render(){
        if(!this.props.openedModal) return (<div></div>);
        const project = this.props.getProject();
        return (
            <div id="myModal" onClick={()=>{this.hideModal()}} className="modal">
                <div className="modal-content" onClick={(e)=>{e.stopPropagation();}}>
                    <span onClick={()=>{this.hideModal()}} className="close">&times;</span>
                    <div className="modal-detail">
                        <div className="modal-images">
                            {project.images.map(p=>{
                                return (
                                    <img src={p} width="100%" />
                                )
                            })}
                        </div>
                        <div className="modal-description">
                            <h3>Skills</h3>
                        <div className='skills'>{project.skills?project.skills.map(s=>{return(<b>{s}, </b>);}):''}</div>
                            <h3>Description</h3>
                            <div>
                                <p>{project.description || ''}</p>
                                <a href={project.project_link}>{project.project_link}</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default connect(state => ({
    getProject: ()=>{
        return state.profile.projects.find(it=>it.index === state.profile.selectedProject);
    },
    openedModal: state.profile.openedModal
}))(ProfileModal)