import React from 'react'
import { connect } from 'react-redux'
// import {ProfilePicture} from './components/profile_picture';
import ProfileHeader from '../components/profile_header';
//import ProfileCoverPhoto from './components/profile_cover_photo';
import ProfilePhoto from '../components/profile_photo';
import ProfileSection from '../components/profile_section';
//import ProfileAbout from './components/profile_about'
import ProfileExperience from '../components/profile_experience'
//import ProfileEducation from './components/profile_education'
import ProfileReference from '../components/profile_reference'

class Profile extends React.Component {
    render() {
        if(this.props.openedModal) return (<div></div>)
        return (
          <div id="appContent" className="App-content">
            <ProfileHeader />
            <ProfilePhoto/>
            {/* <ProfilePicture url = '/photo.png' altText='Zemin Wang'/> */}
            {/* <ProfileSection id="about_section">
                <ProfileAbout/>
            </ProfileSection> */}
            <ProfileSection id="experience_section">
                <ProfileExperience/>
            </ProfileSection>
            {/* <ProfileSection id="education_section">
                <ProfileEducation/>
            </ProfileSection> */}
            {/* <ProfileSection id="contact_section">
                <ProfileReference/>
            </ProfileSection> */}
            {/* <div className="App-footer">
                Copyright@2020
            </div> */}
          </div>        
        )
    }
}

export default connect(state => ({
    openedModal: state.profile.openedModal
}))(Profile)